export default {
	data() {
		return {
			StatusBar: this.StatusBar,
			CustomBar: this.CustomBar
		};
	},
	name: 'cu-custom',
	computed: {
		style() {
			var StatusBar = this.StatusBar;
			var CustomBar = this.CustomBar;
			var bgImage = this.bgImage;
			// console.log("Stats=========>",statusbar)
			var style = `height:${CustomBar}px;padding-top:${StatusBar}px;`;
			if (this.bgImage) {
				style =
					`${style}background-image:url(${bgImage}) ;background-image: url(${bgImage});background-position: 40rpx ${(CustomBar-16+StatusBar)/2}px ;background-size:32rpx 32rpx;background-repeat:no-repeat;`;
			}
			return style
		}
	},
	props: {
		bgColor: {
			type: String,
			default: ''
		},
		isBack: {
			type: [Boolean, String],
			default: false
		},
		bgImage: {
			type: String,
			default: ''
		},
	},
	methods: {
		BackPage() {
			console.log("返回")
			uni.navigateBack({
				delta: 1
			});
		},
		scan() {
			if (this.bgImage) {
				this.$emit("scan")
			}
		}
	}
}
